import React, {Component, PureComponent} from 'react';
import ReactDOM from 'react-dom';
import Finance from 'financejs';

import'./GuansInvestmentTax.css';

import T5 from './T5';


import {Container, Collapse,  CardBody, Navbar, NavbarToggler, NavbarBrand, Nav, NavItem, NavLink, UncontrolledDropdown, DropdownToggle, DropdownMenu,DropdownItem, Row, Col,Form, Label, Input, CustomInput, FormText, FormGroup,  Button,Card} from 'reactstrap';



class GuansInvestmentTax extends Component
{
    constructor(props)
    {
        super(props);
        this.state = 
        {

            isT4Selected:false,
            isT5Selected:false,
            //About You
            AboutYou_FirstName:"",AboutYou_MiddleName:"",AboutYou_LastName:"",
            AboutYou_SIN:"",
            AboutYou_BirthDate:"",
            AboutYou_DeathDate:"",
            AboutYou_PreferredLanguage:"English",

            //Mailing Address
            MailingAddress_CareOf:"",
            MailingAddress_Apartment:"",MailingAddress_StreetNumber:"",MailingAddress_StreetName:"",
            MailingAddress_City:"",MailingAddress_ProvOrTerri:"",MailingAddress_PostalCode:"",
            MailingAddress_HomeTelA:"",MailingAddress_HomeTelB:"",

            //About Your Residence

            AboutYourResidence_ProvOrTerri:"",
            AboutYourResidence_MoveDate:"",
            AboutYourResidence_Same:"",
            AboutYourResidence_CurrentResidence:"",
            AboutYourResidence_TaxPurposes:"",

            //You and Your Family

            YouAndYourFamily_MaritalStatus:"",
            YouAndYourFamily_MaritalChange:"",
            YouAndYourFamily_Dependents:"",

            //Misc

            Misc_SpecifiedForeignProperty:"",
            Misc_Dispose:"",
            Misc_FirstTime:"",
            Misc_Citizen:"",
            Misc_IndianAct:"",
            Misc_Prison:""

        }

        this.handleChange = this.handleChange.bind(this);
        this.handleKeyPress = this.handleKeyPress.bind(this);
        this.flipT4 = this.flipT4.bind(this);
        this.flipT5 = this.flipT5.bind(this);


    }


    handleChange(event)
    {
        const name = event.target.name;
        this.setState({[name]: event.target.value});
    }

    handleKeyPress(e)
    {
        ///THIS WORKS!!!!!///
        if(e.key==='Enter')
        {e.preventDefault();}
        if(e.key===',')
        {
            e.preventDefault();
            alert("Commas are not allowed to be entered into the report.");
        }
    }

    flipT4()
    {
      var currentT4State = this.state.isT4Selected;
      this.setState({isT4Selected:!currentT4State});
    }

    flipT5()
    {
      var currentT5State = this.state.isT5Selected;
      this.setState({isT5Selected:!currentT5State});
    }



    render()
    {
        return(
            <div className = "form-vertical">
            <body>

         
        <Navbar color="light" light expand="md">
          <NavbarBrand >Tax123 - Guans Investment Tax</NavbarBrand>
          <NavbarToggler onClick={this.toggle} />
          <Collapse isOpen={this.state.isOpen} navbar>
            <Nav className="ml-auto" navbar>
              <NavItem>
                <NavLink >All Your Taxes</NavLink>
              </NavItem>
              <NavItem>
                <NavLink >Done Here!</NavLink>
              </NavItem>
              <UncontrolledDropdown nav inNavbar>
                <DropdownToggle nav caret>
                  Options
                </DropdownToggle>
                <DropdownMenu right>
                  <DropdownItem>
                    Option 1
                  </DropdownItem>
                  <DropdownItem>
                    Option 2
                  </DropdownItem>
                  <DropdownItem divider />
                  <DropdownItem>
                    Reset
                  </DropdownItem>
                </DropdownMenu>
              </UncontrolledDropdown>
            </Nav>
          </Collapse>
        </Navbar>
            <h1 id = "Title">My 2018 Tax Return</h1>


<Card>
  <CardBody>


            <h1 id = "SimpleTaxText">About You</h1>

            <h3 id="SimpleTaxText">Name *</h3>

            <Row id = "regularInputRow">

            
                
            <Form><Input id = "inputField" name="AboutYou_FirstName" type="string" placeholder="First Name" onChange={this.handleChange} onKeyPress={this.handleKeyPress}></Input></Form>
            <Form><Input id = "inputField" name="AboutYou_MiddleName" type="string" placeholder= "Middle Name" onChange={this.handleChange} onKeyPress={this.handleKeyPress}></Input></Form>
            <Form><Input id = "inputField" name="AboutYou_LastName" type="string" placeholder="Last Name" onChange={this.handleChange} onKeyPress={this.handleKeyPress}></Input></Form>

            </Row>
            <h3 id="SimpleTaxText">Social insurance number *</h3>
            <Row id = "regularInputRow">   
            <Form><Input id = "inputField" name="AboutYou_SIN" type="string" onChange={this.handleChange} onKeyPress={this.handleKeyPress}></Input></Form>
            </Row>
            <h3 id="SimpleTaxText">Date of birth *</h3>
            <Row id="regularInputRow">
                <Form><Input id="inputField" name="AboutYou_BirthDate" type="string" placeholder="YYYY-MM-DD" onChange={this.handleChange} onKeyPress={this.handleKeyPress}></Input></Form>
            </Row>

            <h3 id="SimpleTaxText">If this return is for a deceased person, date of death</h3>
            <Row id="regularInputRow">
                <Form><Input id="inputField" name="AboutYou_DeathDate" type="string" placeholder="YYYY-MM-DD" onChange={this.handleChange} onKeyPress={this.handleKeyPress}></Input></Form>
            </Row>
            <h3 id="SimpleTaxText">Preferred Language</h3>
            <Row>
            <CustomInput type="select" id="inputField" name="AboutYou_PreferredLanguage" onChange={this.handleChange} onKeyPress={this.handleKeyPress}>
            <option value="">Select</option>
            <option>English</option>
            <option>French</option>
            <option>Chinese</option>

            </CustomInput>
            </Row>


            </CardBody>
            </Card>

            <Card>
              <CardBody>
              <h1 id = "SimpleTaxText">Mailing Address</h1>
            <Row>
              <Form><Input id="inputField" name="MailingAddress_CareOf" type="string" placeholder="C/O Care Of" onChange={this.handleChange} onKeyPress={this.handleKeyPress}></Input></Form>
            </Row>
            <Row>
              <Form><Input id="inputField" name="MailingAddress_Apartment" type="string" placeholder="Apartment" onChange={this.handleChange} onKeyPress={this.handleKeyPress}></Input></Form>
              <Form><Input id="inputField" name="MailingAddress_StreetNumber" type="string" placeholder="Street number" onChange={this.handleChange} onKeyPress={this.handleKeyPress}></Input></Form>
              <Form><Input id="inputField" name="MailingAddress_StreetName" type="string" placeholder="Street name*" onChange={this.handleChange} onKeyPress={this.handleKeyPress}></Input></Form>
            </Row>
            <h3 id="SimpleTaxText">Home Telephone</h3>
            <Row>
              <Form><Input id="inputField" name="MailingAddress_HomeTelA" type="string" onChange={this.handleChange} onKeyPress={this.handleKeyPress}></Input></Form>
              <Form><Input id="inputField" name="MailingAddress_HomeTelB" type="string"  onChange={this.handleChange} onKeyPress={this.handleKeyPress}></Input></Form>
              
            </Row>
              </CardBody>
            </Card>
            <Card>
              <CardBody>
                <h1 id = "SimpleTaxText">AboutYourResidence</h1>
                <h3 id="SimpleTaxText">In which province or territory did you live on December 31 2018?</h3>
                <CustomInput type="select" id="inputField" name="AboutYourResidence_ProvOrTerri" onChange={this.handleChange} onKeyPress={this.handleKeyPress}>
                <option value="">Select</option>
                <option>Alberta</option>
                <option>British Columbia</option>
                <option>Manitoba</option>
                <option>New Brunswick</option>

            </CustomInput>
            <h3 id="SimpleTaxText">If your province or territory of residence changed in 2018, enter the date of your move</h3>

            <Form><Input id="inputField" name="AboutYourResidence_MoveDate" type="string"  onChange={this.handleChange} onKeyPress={this.handleKeyPress}></Input></Form>
            <h3 id="SimpleTaxText">Is your home address the same as your mailing address?</h3>
            <CustomInput type="select" id="inputField" name="AboutYourResidence_Same" onChange={this.handleChange} onKeyPress={this.handleKeyPress}>
                <option value="">Select</option>
                <option>Yes</option>
                <option>No</option>


            </CustomInput>
            <h3 id="SimpleTaxText">Current Residence</h3>
            <CustomInput type="select" id="inputField" name="AboutYourResidence_CurrentResidence" onChange={this.handleChange} onKeyPress={this.handleKeyPress}>
                <option value="">Select</option>
                <option>Alberta</option>
                <option>British Columbia</option>
                <option>Manitoba</option>
                <option>New Brunswick</option>

            </CustomInput>

            <h3 id="SimpleTaxText">Did you become a resident of Canada (immigrate) for tax purposes in 2018?</h3>
            <CustomInput type="select" id="inputField" name="AboutYourResidence_TaxPurposes" onChange={this.handleChange} onKeyPress={this.handleKeyPress}>
                <option value="">Select</option>
                <option>Yes</option>
                <option>No</option>


            </CustomInput>
              </CardBody>
            </Card>

            <Card>
              <CardBody>
              <h1 id = "SimpleTaxText">You and Your Family</h1>
              
              <h3 id="SimpleTaxText">Marital Status on December 31, 2018</h3>
            <CustomInput type="select" id="inputField" name="YouAndYourFamily_MaritalStatus" onChange={this.handleChange} onKeyPress={this.handleKeyPress}>
                <option value="">Select</option>
                <option>Married</option>
                <option>Living Common-Law</option>
                <option>Widowed</option>
                <option>Divorced</option>
                <option>Separated</option>
                <option>Single</option>


            </CustomInput>

            <h3 id="SimpleTaxText">Did your marital status change in 2018?</h3>
            <CustomInput type="select" id="inputField" name="YouAndYourFamily_MaritalChange" onChange={this.handleChange} onKeyPress={this.handleKeyPress}>
                <option value="">Select</option>
                <option>Yes</option>
                <option>No</option>


            </CustomInput>

            <h3 id="SimpleTaxText">Do you have any dependents?</h3>
            <CustomInput type="select" id="inputField" name="YouAndYourFamily_Dependents" onChange={this.handleChange} onKeyPress={this.handleKeyPress}>
                <option value="">Select</option>
                <option>Yes</option>
                <option>No</option>
            </CustomInput>

              </CardBody>
            </Card>

            <Card>
              <CardBody>
              <h1 id = "SimpleTaxText">Misc</h1>

              <h3 id="SimpleTaxText">Did you own specified foreign property with a total cost of more than $100,000 in 2018?</h3>
              <CustomInput type="select" id="inputField" name="Misc_SpecifiedForeignProperty" onChange={this.handleChange} onKeyPress={this.handleKeyPress}>
                <option value="">Select</option>
                <option>Yes</option>
                <option>No</option>
            </CustomInput>

            <h3 id="SimpleTaxText">Did you dispose of your principal residence in 2018?</h3>
            <CustomInput type="select" id="inputField" name="YMisc_Dispose" onChange={this.handleChange} onKeyPress={this.handleKeyPress}>
                <option value="">Select</option>
                <option>Yes</option>
                <option>No</option>
            </CustomInput>

            <h3 id="SimpleTaxText">Are you filing an income tax return with the CRA for the first time?</h3>
            <CustomInput type="select" id="inputField" name="Misc_FirstTime" onChange={this.handleChange} onKeyPress={this.handleKeyPress}>
                <option value="">Select</option>
                <option>Yes</option>
                <option>No</option>
            </CustomInput>

            <h3 id="SimpleTaxText">Are you a Canadian citizen?</h3>
            <CustomInput type="select" id="inputField" name="Misc_Citizen" onChange={this.handleChange} onKeyPress={this.handleKeyPress}>
                <option value="">Select</option>
                <option>Yes</option>
                <option>No</option>
            </CustomInput>

            <h3 id="SimpleTaxText">Are you a person registered under the Indian Act?</h3>
            <CustomInput type="select" id="inputField" name="Misc_IndianAct" onChange={this.handleChange} onKeyPress={this.handleKeyPress}>
                <option value="">Select</option>
                <option>Yes</option>
                <option>No</option>
            </CustomInput>

            <h3 id="SimpleTaxText">Were you confined to a prison for a period of 90 days or more in 2018?</h3>
            <CustomInput type="select" id="inputField" name="Misc_Prison" onChange={this.handleChange} onKeyPress={this.handleKeyPress}>
                <option value="">Select</option>
                <option>Yes</option>
                <option>No</option>
            </CustomInput>

              </CardBody>
            </Card>
            <Row>
            <Button onClick={this.flipT4}>TOGGLE T4</Button>
            <Button onClick = {this.flipT5}>Toggle T5</Button>
            <Button >Toggle T2202A</Button>
            </Row>
            
            {this.state.isT4Selected ? 
            <h1>T4 FORM GOES HERE</h1>
            :null}
            {this.state.isT5Selected ?
            <h1><T5/></h1>
          :null}
           

        </body>
        </div>
        );
    }
}
export default GuansInvestmentTax;