import React, {Component, PureComponent} from 'react';
import {Container, Collapse,  CardBody, Navbar, NavbarToggler, NavbarBrand, Nav, NavItem, NavLink, UncontrolledDropdown, DropdownToggle, DropdownMenu,DropdownItem, Row, Col,Form, Label, Input, CustomInput, FormText, FormGroup,  Button,Card} from 'reactstrap';
import './T5.css';
class T5 extends Component{

    constructor(props)
    {
        super(props);

        this.state={

            T5_Name_of_payer:"", T5_Exchange_rate:"",
            T5_24:"", T5_25:"", T5_26:"", T5_13:"", T5_18:"",
            T5_10:"", T5_11:"", T5_12:"",
            T5_OtherBox_1:0,
            T5_OtherBox_2:0,

            T5_JointAcc_Share:0
        }

        this.handleChange = this.handleChange.bind(this);
        this.handleKeyPress = this.handleKeyPress.bind(this);
    }

    handleChange(event)
    {
        const name = event.target.name;
        this.setState({[name]: event.target.value});
    }

    handleKeyPress(e)
    {
        ///THIS WORKS!!!!!///
        if(e.key==='Enter')
        {e.preventDefault();}
        if(e.key===',')
        {
            e.preventDefault();
            alert("Commas are not allowed to be entered into the report.");
        }
    }

    render()
    {
        return(
            <div className = "form-vertical">
                <body>
                <Card>
                    <CardBody>
              <h3 id="SimpleTaxText">T5: Statement of Investment Income</h3>
            <Row>
              <text>Name of payer</text>
              <Form><Input type="string" id="inputField" name="T5_Name_of_payer" onChange={this.handleChange} onKeyPress={this.handleKeyPress}></Input></Form>

              <text>Exchange rate</text>
              <Form><Input type="string" id="inputField" name="T5_Exchange_rate" onChange={this.handleChange} onKeyPress={this.handleKeyPress}></Input></Form>

            </Row>

            <Row>
              <h3>24</h3>
              <Form><Input type="string" id="inputField" name="T5_24" onChange={this.handleChange} onKeyPress={this.handleKeyPress}></Input></Form>

              <text>25</text>
              <Form><Input type="string" id="inputField" name="T5_25" onChange={this.handleChange} onKeyPress={this.handleKeyPress}></Input></Form>

              <text>26</text>
              <Form><Input type="string" id="inputField" name="T5_26" onChange={this.handleChange} onKeyPress={this.handleKeyPress}></Input></Form>

              <text>13</text>
              <Form><Input type="string" id="inputField" name="T5_13" onChange={this.handleChange} onKeyPress={this.handleKeyPress}></Input></Form>

              <text>18</text>
              <Form><Input type="string" id="inputField" name="T5_18" onChange={this.handleChange} onKeyPress={this.handleKeyPress}></Input></Form>
            </Row>

            <Row>
              <text>10</text>
              <Form><Input type="string" id="inputField" name="T5_10" onChange={this.handleChange} onKeyPress={this.handleKeyPress}></Input></Form>

              <text>11</text>
              <Form><Input type="string" id="inputField" name="T5_11" onChange={this.handleChange} onKeyPress={this.handleKeyPress}></Input></Form>

              <text>12</text>
              <Form><Input type="string" id="inputField" name="T5_12" onChange={this.handleChange} onKeyPress={this.handleKeyPress}></Input></Form>

            </Row>
            
            <text>Other boxes on your T5</text>
            <Row>
              <Form><Input id="inputField" name="T5_OtherBox_1" type="number" placeholder="Box"></Input></Form>

              <Form><Input type="number" id="inputField" name="T5_OtherBox_2" onChange={this.handleChange} onKeyPress={this.handleKeyPress}></Input></Form>

            </Row>

            
            <Row>
              <text>If this is a joint account, your share</text>
              <Form><Input id="inputField" name="T5_JointAcc_Share" type="number" ></Input></Form>
              <text>%</text>
              
            </Row>
           </CardBody>
            </Card>
            </body>
            </div>
        );
    }
}
export default T5;